.container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: auto;
    max-width: 1240px;
  }
  
  ::-webkit-scrollbar {
    width: 8px;
    background-color: #2f2f37;
  }
  
  ::-webkit-scrollbar-thumb {
    background-color: #8585ad;
  }
  